.carrousel {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    border-radius: 20px;
    background-color: #E3E3E3;
}

.carrousel img {
    width:100%;
    height: 100%;
    object-fit: cover;
    border-radius: 20px;
}

.chevron {
    display: flex;
    position: absolute;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 80px;
    padding: 0 25px;
}

.chevron i {
    font-size: 80px;
    color: #ffffff;
    cursor: pointer;
}

.carrousel p {
    display: flex;
    position: absolute;
    top: 90%;
    text-align: center;
    color: #ffffff;
}

@media screen and (max-width:767px) and (min-width:320px) { 
.chevron i {
    font-size: 30px;
}


}